.a-dance {
  opacity: 1 !important;
  visibility: visible !important;
  animation: dance 2s linear 1;
}

@keyframes dance {
  0%,
  10%,
  20% {
    transform: rotateZ(10deg);
  }
  5%,
  15%,
  25% {
    transform: rotateZ(-10deg);
  }
  35%,
  60%,
  100% {
    transform: scale(1) rotateZ(0deg);
    box-shadow: inset 0 0 0 0 transparent;
  }
  40% {
    transform: scale(1.3);
    box-shadow: inset 0 0 0 0;
    box-shadow: inset 0 0 0 2px rgba(0, 0, 0, 0.1);
  }
}

.a-journal {
  opacity: 1 !important;
  visibility: visible !important;
  animation: journal 2s ease-in 1;
}

@keyframes journal {
  0% {
    transform: scale(0) rotate(-360deg);
  }
  80% {
    transform: scale(0.5) rotate(-180deg);
  }
  100% {
    transform: scale(1) rotate(0);
  }
}

.a-pulse {
  opacity: 1 !important;
  visibility: visible !important;
  animation: pulse infinite linear 0.5s alternate;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.05);
  }
}

.a-pulse-slow {
  opacity: 1 !important;
  visibility: visible !important;
  animation: pulseSlow alternate infinite ease-in-out 1s;
}

@keyframes pulseSlow {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  100% {
    transform: scale(0.85);
    opacity: 0.5;
  }
}

@keyframes jamp {
  0% {
    transform: translateY(0);
  }
  10% {
    transform: translateY(-50px);
  }
  25% {
    transform: translateY(-50px) scale(1.1);
  }
  35% {
    transform: translateY(-50px) scale(1);
  }
  45% {
    transform: translateY(-50px) scale(1.2);
  }
  55% {
    transform: translateY(-50px) scale(1);
  }
  65% {
    transform: translate(150px, -50px) scale(0.8);
  }
  85% {
    transform: translate(150px, -50px) scale(1.2);
  }
  95% {
    transform: translate(0, 0) scale(1);
  }
  100% {
    transform: translate(0, 0);
  }
}

.a-jamp {
  opacity: 1 !important;
  visibility: visible !important;
  animation: jamp 1 linear 1s;
}

@keyframes fourRock {
  0% {
    transform-origin: 0 0;
  }
  5% {
    transform: scale(0.5);
  }
  10% {
    transform: scale(0.5);
  }
  15% {
    transform: scale(0.5) translateX(130px);
  }
  20% {
    transform: scale(0.5) translateX(130px);
  }
  25% {
    transform: scale(0.5) translate(130px, 130px);
  }
  30% {
    transform: scale(0.5) translate(0, 130px);
  }
  35% {
    transform: scale(0.5) translate(0, 0);
  }
  40% {
    transform: scale(1);
  }
  100% {
    transform: scale(1);
    transform-origin: 0 0;
  }
}

.a-four-rock {
  opacity: 1 !important;
  visibility: visible !important;
  animation: fourRock 1 ease-out 2s;
}

.a-enter-up-bounce {
  opacity: 1 !important;
  visibility: visible !important;
  animation: enterUpBounce 1 linear 0.8s;
}

@keyframes enterUpBounce {
  0% {
    transform: translateY(120px);
    opacity: 0;
  }
  50% {
    transform: translateY(0);
  }
  70% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.a-enter-down-bounce {
  opacity: 1 !important;
  visibility: visible !important;
  animation: enterDownBounce 1 linear 0.8s;
}

@keyframes enterDownBounce {
  0% {
    transform: translateY(-120px);
    opacity: 0;
  }
  50% {
    transform: translateY(0);
  }
  70% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

.a-enter-left-bounce {
  opacity: 1 !important;
  visibility: visible !important;
  animation: enterLeftBounce 1 linear 0.8s;
}

@keyframes enterLeftBounce {
  0% {
    transform: translateX(-120px);
    opacity: 0;
  }
  50% {
    transform: translateX(0);
  }
  70% {
    transform: translateX(-8px);
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.a-enter-right-bounce {
  opacity: 1 !important;
  visibility: visible !important;
  animation: enterRightBounce 1 linear 0.8s;
}

@keyframes enterRightBounce {
  0% {
    transform: translateX(120px);
    opacity: 0;
  }
  50% {
    transform: translateX(0);
  }
  70% {
    transform: translateX(8px);
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.a-jump-bounce {
  opacity: 1 !important;
  visibility: visible !important;
  animation: jumpBounce 1 1.5s ease-out;
}

@keyframes jumpBounce {
  10% {
    transform: translateY(-100px);
  }
  20%,
  35%,
  50% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-40px);
  }
  40% {
    transform: translateY(-10px);
  }
}

.a-scale-bounce {
  opacity: 1 !important;
  visibility: visible !important;
  animation: scaleBounce 0.6s ease-in-out;
}

@keyframes scaleBounce {
  0% {
    transform: scale(1.5);
    opacity: 0;
  }
  50% {
    transform: scale(1);
  }
  70% {
    transform: scale(1.1);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.a-perspective {
  /* autoprefixer: off */
  -webkit-perspective: 600;
  -o-perspective: 600;
  -moz-perspective: 600;
  perspective: 600;
}

.a-three-flip-down {
  transform: rotateX(90deg);
  transform-origin: 0 100%;
  opacity: 1 !important;
  visibility: visible !important;
  animation: threeFlipDown 1s ease-in-out 1;
}

@keyframes threeFlipDown {
  0% {
    transform: rotateX(0);
  }
  100% {
    transform: rotateX(-90deg);
  }
}

.a-three-flip-left {
  transform: rotateY(-90deg);
  transform-origin: 0 0;
  opacity: 1 !important;
  visibility: visible !important;
  animation: threeFlipLeft 1s ease-in-out 1;
}

@keyframes threeFlipLeft {
  0% {
    transform: rotateY(0);
  }
  100% {
    transform: rotateY(-90deg);
  }
}

.a-three-flip-right {
  transform: rotateY(90deg);
  transform-origin: 100% 0;
  opacity: 1 !important;
  visibility: visible !important;
  animation: threeFlipRight 1s ease-in-out 1;
}

@keyframes threeFlipRight {
  0% {
    transform: rotateY(0);
  }
  100% {
    transform: rotateY(90deg);
  }
}

.a-three-flip-up {
  transform: rotateX(90deg);
  transform-origin: 100% 0;
  opacity: 1 !important;
  visibility: visible !important;
  animation: threeFlipUp 1s ease-in-out 1;
}

@keyframes threeFlipUp {
  0% {
    transform: rotateX(0);
  }
  100% {
    transform: rotateX(90deg);
  }
}

.a-flip-left-bounce {
  opacity: 1 !important;
  visibility: visible !important;
  animation: flipLeftBounce 1s ease-in-out 1;
}

@keyframes flipLeftBounce {
  0% {
    opacity: 0;
    transform: rotateY(70deg);
  }
  50% {
    opacity: 1;
    transform: rotateY(-5deg);
  }
  80% {
    transform: rotateY(20deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

.a-flip-right-bounce {
  opacity: 1 !important;
  visibility: visible !important;
  animation: flipRightBounce 1s ease-in-out 1;
}

@keyframes flipRightBounce {
  0% {
    opacity: 0;
    transform: rotateY(-70deg);
  }
  50% {
    opacity: 1;
    transform: rotateY(5deg);
  }
  80% {
    transform: rotateY(-20deg);
  }
  100% {
    transform: rotateY(0deg);
  }
}

.a-rotate-flip {
  opacity: 1 !important;
  visibility: visible !important;
  animation: rotateFlip 1s linear 1;
}

@keyframes rotateFlip {
  0% {
    transform-origin: 50%;
  }
  40% {
    transform: rotateY(70deg);
    opacity: 0;
  }
  60% {
    transform: rotateY(70deg);
    opacity: 0;
  }
  100% {
    transform: rotateY(0deg);
    opacity: 1;
  }
}

.a-flip-top {
  opacity: 1 !important;
  visibility: visible !important;
  animation: flipTop 1s ease-out 1;
}

@keyframes flipTop {
  0% {
    transform: translateY(-100px);
    opacity: 0;
  }
  70% {
    transform: translateY(0);
    opacity: 1;
  }
}

.a-flip-bottom {
  opacity: 1 !important;
  visibility: visible !important;
  animation: flipBottom 1s ease-out 1;
}

@keyframes flipBottom {
  0% {
    transform: translateY(100px);
    opacity: 0;
  }
  70% {
    transform: translateY(30px);
    opacity: 1;
  }
  100% {
    transform: translateY(0);
  }
}

.a-flip-left {
  opacity: 1 !important;
  visibility: visible !important;
  animation: flipLeft 1s ease-out 1;
}

@keyframes flipLeft {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }
  70% {
    transform: translateX(0);
    opacity: 1;
  }
}

.a-flip-right {
  opacity: 1 !important;
  visibility: visible !important;
  animation: flipRight 1s ease-out 1;
}

@keyframes flipRight {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }
  70% {
    transform: translateX(0);
    opacity: 1;
  }
}

.a-rotate-out {
  opacity: 0;
  visibility: hidden;
  animation: rotateOut 1.5s ease-in 1;
}

@keyframes rotateOut {
  0% {
    opacity: 1;
    visibility: visible;
  }
  50%,
  100% {
    transform: scale(0) rotateY(189deg);
  }
}

.a-rotate-flip-down {
  opacity: 1 !important;
  visibility: visible !important;
  animation: rotateFlipDown 1s linear 1;
}

@keyframes rotateFlipDown {
  0% {
    transform-origin: 50%;
  }
  40% {
    transform: rotateX(70deg);
    opacity: 0;
  }
  60% {
    transform: rotateX(70deg);
    opacity: 0;
  }
  100% {
    transform: rotateX(0deg);
    opacity: 1;
  }
}

.a-rotate-down-bounce {
  opacity: 0;
  visibility: hidden;
  animation: rotateDowBounce 2s ease-in-out 1;
  transform-origin: 0 0;
}

@keyframes rotateDowBounce {
  0% {
    transform: rotate(0deg);
    opacity: 1;
    visibility: visible;
  }
  20% {
    transform: rotate(25deg);
  }
  40% {
    transform: rotate(30deg);
  }
  10%,
  30%,
  50% {
    transform: rotate(40deg);
    opacity: 1;
    visibility: visible;
  }
  80%,
  100% {
    transform: rotate(30deg) translateY(310px) translateX(230px);
    opacity: 0;
    visibility: hidden;
  }
}

.a-flash-bang {
  opacity: 1 !important;
  visibility: visible !important;
  animation: flashBang 2s linear 1;
}

@keyframes flashBang {
  0%,
  20% {
    opacity: 0;
  }
  10%,
  30%,
  100% {
    opacity: 1;
  }
}

.a-bomb {
  opacity: 0;
  visibility: hidden;
  animation: bomb 1s ease-out 1;
}

@keyframes bomb {
  0% {
    transform-origin: -50% 0%;
    transform: rotateZ(0);
    opacity: 1;
    visibility: visible;
  }
  50%,
  100% {
    transform: rotateZ(-210deg);
    transform-origin: -20% 0%;
  }
  47%,
  100% {
    opacity: 0;
    visibility: hidden;
  }
}

.a-legend-left {
  opacity: 1 !important;
  visibility: visible !important;
}

.a-legend-left::before {
  content: '';
  display: block;
  position: absolute;
  width: 90%;
  height: 64%;
  z-index: 1000;
  left: 0;
  top: 0;
  transform: translateY(9px) translateX(9px);
  background: rgba(0, 0, 0, 0.5);
  animation: legendLeft 0.5s ease-out 1;
}

@keyframes legendLeft {
  0% {
    transform: translateY(9px) translateX(0);
    width: 0;
  }
  100% {
    width: 90%;
    transform: translateY(9px) translateX(9px);
  }
}

.a-legend-right {
  opacity: 1 !important;
  visibility: visible !important;
}

.a-legend-right::before {
  content: '';
  display: block;
  position: absolute;
  width: 90%;
  height: 64%;
  z-index: 1000;
  left: 0;
  top: 0;
  transform: translateY(9px) translateX(9px);
  background: rgba(0, 0, 0, 0.5);
  animation: legendRight 0.5s ease-out 1;
}

@keyframes legendRight {
  0% {
    left: 100%;
    width: 0;
  }
  100% {
    width: 90%;
  }
}

.a-legend-up {
  opacity: 1 !important;
  visibility: visible !important;
}

.a-legend-up::before {
  content: '';
  display: block;
  position: absolute;
  width: 90%;
  height: 64%;
  z-index: 1000;
  transform: translateY(9px) translateX(9px);
  background: rgba(0, 0, 0, 0.5);
  animation: legendUp 0.5s ease-out 1;
}

@keyframes legendUp {
  0% {
    top: 70%;
    height: 0;
  }
  100% {
    height: 64%;
    top: 0;
  }
}

.a-legend-down {
  opacity: 1 !important;
  visibility: visible !important;
}

.a-legend-down::before {
  content: '';
  display: block;
  position: absolute;
  width: 90%;
  height: 64%;
  z-index: 1000;
  transform: translateY(9px) translateX(9px);
  background: rgba(0, 0, 0, 0.5);
  animation: legendDown 0.5s ease-out 1;
}

@keyframes legendDown {
  0% {
    transform: translateY(0) translateX(9px);
    height: 0;
  }
  100% {
    height: 64%;
    transform: translateY(9px) translateX(9px);
  }
}

.a-portrait-before {
  opacity: 1 !important;
  visibility: visible !important;
}

.a-portrait-before::before {
  content: '';
  display: block;
  position: absolute;
  width: 90%;
  height: 64%;
  z-index: 1000;
  transform: translateY(9px) translateX(9px);
  box-shadow: inset 0 0 0 100px rgba(0, 0, 0, 0.5);
  animation: portraitBefore 0.5s ease-out 1;
}

@keyframes portraitBefore {
  0% {
    box-shadow: inset 0 0 0 0 rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 74%;
    transform: translateY(0) translateX(0);
  }
  100% {
    box-shadow: inset 0 0 0 100px rgba(0, 0, 0, 0.5);
    width: 90%;
    height: 64%;
    transform: translateY(9px) translateX(9px);
  }
}

.a-portrait-blur {
  opacity: 1 !important;
  visibility: visible !important;
}

.a-portrait-blur::before {
  content: '';
  display: block;
  position: absolute;
  width: 90%;
  height: 64%;
  z-index: 1000;
  transform: translateY(9px) translateX(9px);
  box-shadow: inset 0 0 100px 100px rgba(0, 0, 0, 0.5);
  animation: portraitBlur 0.5s ease-out 1;
}

@keyframes portraitBlur {
  0% {
    box-shadow: inset 0 0 0 0 rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 74%;
    transform: translateY(0) translateX(0);
  }
  100% {
    box-shadow: inset 0 0 100px 100px rgba(0, 0, 0, 0.5);
    width: 90%;
    height: 64%;
    transform: translateY(9px) translateX(9px);
  }
}

.a-scale-before {
  opacity: 1 !important;
  visibility: visible !important;
}

.a-scale-before::before {
  content: '';
  display: block;
  position: absolute;
  width: 90%;
  height: 64%;
  z-index: 1000;
  transform: translateY(9px) translateX(9px);
  background: rgba(0, 0, 0, 0.5);
  animation: scaleBefore 0.5s ease-out 1;
}

@keyframes scaleBefore {
  0% {
    transform: translateY(9px) translateX(9px) scale(1.5);
  }
  100% {
    transform: translateY(9px) translateX(9px) scale(1);
  }
}
